.header {
  color: gold;
  background-color: black;
  position: fixed; /* this makes the header stay at the top of the page */
  top: 0; /* aligns the header to the top of the page */
  left: 0; /* aligns the header to the left of the page */
  width: 100%; /* spans the entire width of the page */
  height: 45px; /* sets the height of the header */
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 6px;
  padding-right: 6px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* style for the first division */
.division1 {
    position: absolute; /* this positions the division beneath the header */
    background-color: transparent;
    background-size: cover;
    background-position: center;
    left: 0; /* aligns the division to the left of the page */
    top: 45px; /* positions the division 50px beneath the header */
    width: 39%; /* spans a third of the width of the page */
    height: calc(100% - 45px); /* spans the remaining length of the page */
    display: flex;
    flex-direction: column;
}

